        * {
            @import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap');
            padding: 0;
            margin: 0;
            font-family: "Quicksand", sans-serif;
        }

        body {
            background-color: #f3f2ef;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            height: 100vh;
            width: 100vw;
        }

        html,
        body {
            font-size: 62.5%;
            height: 100%;
        }

        html {
            background-color: #eee;
        }

        .box {
            width: 20vw;
            height: 60vh;
            border-radius: 0.5rem;
            box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
            background: #a5bbdd;
            position: relative;
            overflow: hidden;
            transform: translate3d(0, 0, 0);
            min-width: 20rem;
            min-height: 35rem;

        }

        .inputData input {
            border: solid 1px black;
            border-radius: 2rem;
            width: 50%;
            margin: 10px;
            padding: 12px;
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
        .info{
            position: absolute;
        bottom: 0;
            width: 100%;
            height: 45%;
            z-index: 4;

        }
        .wave{
            opacity: 0.3;
            position: absolute;
            top: 120%;
            left: 50%;
            background: white;
            width: 50rem;
            height: 50rem;
            margin-left: -25rem;
            margin-top: -25rem;
            transform-origin: 50% 48%;
            border-radius: 43%;
            animation: drift 3000ms infinite linear;
            z-index: 1;
        }
        .wave.-three{
            animation: drift 5000ms infinite linear;
            z-index: 2 !important;
            opacity: 0.2;
        }
        .wave.-two{
            animation: drift 7000ms infinite linear; 
            opacity: 0.1;
            z-index: 3 !important; 
        }
        #weathercon{
            height: 55%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 3em;
            margin-top: 8rem;
        }
        #weathercon .fas{
            color: #eccc68;
            font-size: 6rem;
            animation: fas-anime 3s linear infinite alternate;
       }
       @keyframes fas-anime {
        from{
            transform: scale(1.1);
        }
        to{
            transform: scale(1.5);
        }
    }
       
        .box:after{
            content: "";
            display: block;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 11;
            transform: translate3d(0, 0, 0);
        }
        .errmsg{
            font-size: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        @keyframes drift {
            from{
                transform: rotate(0deg);
            }
            to{
                transform: rotate(360deg);
            }
        }
        .fa-street-view{
            animation: rotates 3s linear infinite alternate;
        }
        @keyframes rotates {
            from{
                transform: translateX(-0.5rem)
            }
            to{
                transform: translateX(0.5rem);
            }
        }
        .temp{
            margin-top: 2.5rem;
            text-align: center;
            font-size: 3rem;
        }
        .tempmin_max{
            text-align: center;
            margin-top: 0.3rem;
            font-weight: 300;
            font-size: 1.2rem;
            color: #57606f;
        }
        .location{
            margin-top: 1.5rem;
            text-align: center;
         /* font-weight: 800;  */
            font-size: 3rem;
            font-weight: 600;
    
        }
        @media (max-width:600px) {
            .box{
                width: 90vw;
                height: 80vh;
            }
        
        .wave{
            top: 85%;
        }
        /* #weathercon{
            font-size: 5em;
        } */
        .info{
            font-size: 1.5rem;
        }
    }
    @media (max-width:500px) {
            .box{
                height: 80vh;
            }
        
        .wave{
            top: 115%;
        }
    }
    body > span{
        width: 100vw;
        text-align: center;
        color: gray;
    }